import React, { useState } from 'react';
import Select from 'react-select';
import Chip from '../components/Chip';

const filterOptions = [
	{ value: 'difficulty', label: ' kh' },
	{ value: 'class', label: 'Lp' },
];
const sortOptions = [
	{ value: 'most_related', label: 'Ph hp nht' },
	{ value: 'newest', label: 'Mi nht' },
];

export default function Filters() {
	const [filter, setFilter] = useState(null);
	const [sort, setSort] = useState(null);

	return (
		<div className='filters-bg'>
			<div className='filters'>
				<div className='filters__title'>
					<h2 className='h2'>Danh sch bi tp</h2>
					<p>C tt c 300 bi tp trong danh sch</p>
				</div>
				<div className='filters__item'>
					<Select
						className='filters__filter'
						value={filter}
						placeholder='B lc'
						onChange={(selectedValue) => setFilter(() => selectedValue)}
						options={filterOptions}
					/>
					<Select
						className='filters__sort'
						value={sort}
						placeholder='Sp xp theo'
						onChange={(selectedValue) => setSort(() => selectedValue)}
						options={sortOptions}
					/>
				</div>

				<div className='filters__search input-group'>
					<input
						type='text'
						className='form-control'
						placeholder='Nhp t kha cn tm kim...'
					/>
				</div>
				<div className='filters__chip'>
					<Chip name='Ha hc' onClick={() => {}} />
					<Chip name='Sinh hc' onClick={() => {}} />
					<button type='button' className='btn btn--grey'>
						Xa b lc
					</button>
				</div>
			</div>
		</div>
	);
}
