import React from 'react';
import {
	faSearch,
	faBell,
	faShoppingCart,
} from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import Nav from '../components/Nav';
import Logo from '../components/Logo';
import profile from '../../public/images/profile.png';

function Header() {
	return (
		<header className='header'>
			<div className='header__top-bg'>
				<div className='header__top'>
					<div className='support'>
						Hotline: 1900-0000{' '}
						<span className='support support--time'>
							(Thi gian h tr t 7h - 22h)
						</span>
					</div>
					<div className='profile'>
						<img
							src={profile}
							alt='profile avatar'
							className='profile__img'
						/>
						<span className='profile__name'>Xin cho, b dng</span>
						<FontAwesomeIcon className='notification' icon={faBell} />
					</div>
				</div>
			</div>
			<div className='header__main'>
				<Logo />
				<div className='header__search input-group'>
					<input
						type='text'
						className='form-control'
						placeholder='Nhp ID ca cu hi cn xem p n v li gii...'
						aria-label='Username'
						aria-describedby='basic-addon1'
					/>
					<div className='input-group-append'>
						<button className='btn btn--padding-sm' type='button'>
							<FontAwesomeIcon icon={faSearch} />
						</button>
					</div>
				</div>
				<button type='button' className='btn-text btn-text--highlight'>
					<FontAwesomeIcon className='mr-tn' icon={faShoppingCart} />
					<span className='cart__text'>Cc kha hc  chn</span>
				</button>
			</div>
			<Nav />
		</header>
	);
}

export default Header;
